.features {
  min-width: 1170px;
  max-width: 1400px;
  margin: 0 auto;

  background-color: $features-section-background-color;
}

.features-container {
  width: 960px;
  margin: 0 auto;

  background-color: $features-section-background-color;
}

.features-intro {
  display: block;
  padding-top: 114px;

  &__header {
    font-weight: bold;
    font-size: 32px;
    line-height: 32px;
    font-family: "PT Sans", "Tahoma", sans-serif;
    text-align: center;
    color: $font-color-black;
  }

  &__text {
    width: 740px;
    margin: 0 auto;
    margin-top: 15px;

    font-weight: 400;
    font-size: 20px;
    line-height: 24px;
    font-family: "PT Sans", "Tahoma", sans-serif;
    text-align: center;
    color: $font-color-dark;
  }
}

.features-blocks {
  display: block;
  margin: 0 auto;
  padding-top: 85.5px;
  padding-bottom: 163px;
}

.features-block {
  display: inline-block;

  &__header {
    font-weight: normal;
    font-size: 22px;
    line-height: 24px;
    font-family: "PT Sans", "Tahoma", sans-serif;
    color: $font-color-black;
  }

  &__text {
    width: 215px;
    margin-top: 12px;

    font-weight: normal;
    font-size: 16px;
    line-height: 20px;
    font-family: "PT Sans", "Tahoma", sans-serif;
    text-align: left;
    color: $font-color-light-gray;
  }
}

.features-blocks__first-row {
  display: block;
  width: 960px;
  margin: 0 auto;
}

.features-blocks__second-row {
  display: block;
  width: 960px;
  margin: 0 auto;
  margin-top: 82px;
}

.features-block__icon-section {
  display: inline-block;

  font-size: 39px;
  vertical-align: top;
}

.features-block__text-section {
  display: inline-block;
}
