.socials {
  min-width: 1170px;
  max-width: 1400px;
  margin: 0 auto;

  background-color: $socials-section-background-color;

  &__facebook {
    margin-right: 34px;
  }

  &__twitter {
    margin-right: 31px;
    margin-left: -4px;
  }

  &__gplus {
    margin-right: 33px;
    margin-left: -4px;
  }

  &__pinterest {
    margin-left: -4px;
  }
}

.socials-container {
  display: block;
  width: 960px;
  margin: 0 auto;
  margin-bottom: -1px;
}

.social-links {
  width: 290px;
  margin: 0 auto;
  padding-top: 80px;
}

.social-link {
  display: inline-block;
}
