.header {
  display: block;
  min-width: 1170px;
  max-width: 1400px;
  margin: 0 auto;
  margin-bottom: -1px;
  padding-top: 59px;

  background-color: $header-background-color;

  &__logo {
    margin-left: 20px;
  }
}

.header-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 960px;
  margin: 0 auto;
}

.logo {
  display: flex;
  align-items: center;

  color: $font-color-black;

  &__brandname {
    margin-left: 11px;

    font-weight: bold;
    font-size: $brandname-font-size;
    line-height: 20px;
    font-family: "PT Sans", "Tahoma", sans-serif;
  }
}
