.subscribe {
  min-width: 1170px;
  max-width: 1400px;
  margin: 0 auto;

  background-color: $subscribe-section-background-color;

  &__form {
    margin-top: 40px;
  }
}

.subscribe-container {
  display: block;
  width: 960px;
  margin: 0 auto;
  margin-bottom: -1px;
}

.subscribe-intro {
  padding-top: 92px;
}

.subscribe-header {
  font-weight: bold;
  font-size: 32px;
  line-height: 32px;
  font-family: "PT Sans", "Tahoma", sans-serif;
  text-align: center;
  color: $font-color-yellow;
}

.subscribe-subheader {
  display: block;
  width: 738px;
  margin: 0 auto;
  padding-top: 15px;

  font-weight: 400;
  font-size: 20px;
  line-height: 24px;
  font-family: "PT Sans", "Tahoma", sans-serif;
  text-align: center;
  color: $font-color-light-gray;
}

.subscribe-form {
  display: block;

  text-align: center;

  &__email {
    width: 577px;
    height: 50px;
    padding: 0 20px;

    font-weight: 400;
    font-size: 16px;
    line-height: 20px;
    font-family: "PT Sans", "Tahoma", sans-serif;

    background-color: #ffffff;
    border-radius: 5px;
  }

  &__btn-submit {
    margin-left: 18px;
  }
}
