.btn-submit {
  width: 126px;
  height: 50px;

  font-weight: bold;
  font-size: 16px;
  line-height: 24px;
  font-family: "PT Sans", "Tahoma", sans-serif;

  background-color: $subscribe-btn-color;
  border-radius: 5px;
  cursor: pointer;

  transition: background-color 0.5s;

  &:hover {
    background-color: $subscribe-btn-hover-color;

    transition: background-color 0.5s;
  }
}
