.banner {
  display: flex;
  min-width: 1170px;
  max-width: 1400px;
  margin: 0 auto;
  padding-top: 63px;

  background-color: $banner-section-background-color;
}

.banner-container {
  width: 960px;
  margin: 0 auto;
}

.banner-text {
  display: inline-block;
  width: 390px;
  margin-top: 63px;
  margin-left: 21px;

  &__header {
    font-weight: bold;
    font-size: 32px;
    font-family: "PT Sans", "Tahoma", sans-serif;
    color: $font-color-black;
    letter-spacing: 0.1px;
  }

  &__subheader {
    margin-top: 14px;

    font-weight: normal;
    font-size: 20px;
    font-family: "PT Sans", "Tahoma", sans-serif;
    color: $font-color-dark;
  }

  &__description {
    width: 393px;
    margin-top: 16px;

    font-size: 16px;
    font-family: "PT Sans", "Tahoma", sans-serif;
    color: $font-color-gray;
  }
}

.banner-icons {
  display: block;
  margin-top: 36px;
}

.tablet-img {
  display: inline-block;
  margin-left: 179px;

  vertical-align: top;

  &__body {
    display: flex;
    align-items: center;
    width: 316px;
    height: 470px;

    background-image: url("../img/tablet-body.png");
    background-repeat: no-repeat;
  }

  &__screen {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 288px;
    height: 382px;
    margin: 0 auto;

    background-color: $tablet-screen;

    transition-duration: 1.5s;
    transition-property: background-color;
  }
}

.tablet-screen-logo {
  display: flex;
  margin-bottom: 33px;

  &__brandname {
    display: inline-block;
    margin-left: 9px;

    font-weight: bold;
    font-size: $brandname-font-size;
    font-family: "PT Sans", "Tahoma", sans-serif;
    color: $font-color-yellow;

    cursor: default;
  }
}

.slider {
  padding-top: 44px;
  padding-bottom: 49px;

  background-color: $banner-section-background-color;
}

.slider-dots {
  display: flex;
  justify-content: center;

  &__dot {
    width: 14px;
    height: 14px;

    list-style-type: none;

    background-color: $slider-dot;
    border-radius: 100%;
    cursor: pointer;
    opacity: 0.5;

    transition-duration: 0.5s;
    transition-property: opacity;

    &:hover {
      opacity: 1;
    }

    &--middle {
      margin-right: 7px;
      margin-left: 7px;
    }
  }
}
