.footer {
  display: block;
  min-width: 1170px;
  max-width: 1400px;
  margin: 0 auto;
  padding-top: 130px;
  padding-bottom: 136px;

  text-align: left;

  background-color: $footer-background-color;
}

.footer-container {
  display: block;
  width: 960px;
  margin: 0 auto;
}

.footer-address {
  display: inline-block;
  margin-left: 25px;

  font-style: normal;

  &__address {
    font-weight: 400;
    font-size: 16px;
    line-height: 20px;
    font-family: "PT Sans", "Tahoma", sans-serif;
    color: $font-color-white;
    letter-spacing: -0.2px;
  }

  &__email {
    font-weight: 400;
    font-size: 16px;
    line-height: 27px;
    font-family: "PT Sans", "Tahoma", sans-serif;
    color: $font-color-white;
  }

  &__tel {
    font-weight: 400;
    font-size: 16px;
    line-height: 20px;
    font-family: "PT Sans", "Tahoma", sans-serif;
    color: $font-color-white;
  }
}
