.nav {
  &__list {
    margin-top: 9px;
    margin-right: 9px;

    list-style-type: none;
  }
}

.nav-item {
  &__features {
    display: inline-block;
    margin-right: 72px;

    font-weight: bold;
    font-size: $nav-item-font-size;
    line-height: 20px;
    font-family: "PT Sans", "Tahoma", sans-serif;
  }

  &__support {
    display: inline-block;
    margin-right: 67px;
    margin-left: -4px;

    font-weight: bold;
    font-size: $nav-item-font-size;
    line-height: 20px;
    font-family: "HelveticaNeue", "Tahoma", sans-serif;
  }

  &__blog {
    display: inline-block;
    margin-left: -4px;

    font-weight: bold;
    font-size: $nav-item-font-size;
    line-height: 20px;
    font-family: "HelveticaNeue", "Tahoma", sans-serif;
  }
}

.nav__link {
  color: $font-color-black;

  transition: color 0.5s;

  &:hover {
    color: red;

    transition: color 0.5s;
  }
}

.footer-nav {
  display: inline-block;
  margin-left: 117px;

  vertical-align: top;

  &__first-section {
    display: inline-block;
    margin-right: 97px;

    vertical-align: top;
  }

  &__second-section {
    display: inline-block;
    margin-right: 108px;
    margin-left: -4px;

    vertical-align: top;
  }

  &__third-section {
    display: inline-block;
    margin-right: 83px;
    margin-left: -4px;

    vertical-align: top;
  }

  &__fourth-section {
    display: inline-block;
    margin-left: -4px;

    vertical-align: top;
  }
}

.footer-nav__list {
  display: inline-block;
}

.footer-nav_list-item {
  list-style-type: none;
}

.footer-nav_link {
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  font-family: "PT Sans", "Tahoma", sans-serif;
  color: $font-color-white;
}
